
@import url('https://fonts.googleapis.com/css2?family=Mochiy+Pop+P+One&family=Questrial&family=Rajdhani:wght@300&display=swap');
:root{

  --color-bg : #ccc;

  --color-text : #2b3341;

  --color-bg-cards : #383836;

  --color-text-description : #ffffff;

  
}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}


html{
  scroll-behavior: smooth;
}

body {
  font-family: 'Questrial', sans-serif;

  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


/* ------------------------- Header ---------------------- */

.header{
  position: fixed;
  z-index: 1000;
  width: 100%;
  transition: 0.3s ease all;
}


.header.active{
  background-color: #fff;
}

.nav{
  margin: 0 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 4.7rem;
}

.content_logo{
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}


.titulo_logo{
  font-size: 2.8rem;
  font-weight: bolder;
  color: #eee;
  font-family: 'Questrial', sans-serif;


}

.header.active .titulo_logo{
  color: #222;
}

.logo_icon{
  font-size: 2.2rem;
  color: #eee;
  padding-right: 5px;


}


.header.active .logo_icon{
  color: #222;
}

.nav_menu{
  display: flex;
  list-style: none;
  gap: 15px;
}

.nav_link , .nav_link a
{
  cursor: pointer;
  font-weight:lighter;
  text-decoration: none;
  font-size: 1.5rem;
  font-weight: bold;
  color: #FFF9;
  font-family: 'Questrial', sans-serif;
  user-select: none;
  transition: .3s ease all;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
}


.header.active .nav_link , .nav_link a{
  color: rgb(169, 169, 169);
}

.nav_link:hover {
  color: #fff;
}


.nav_link:hover .items_link{
  color: #fff;

}


.header.active .nav_link:hover , .header.header.active .icon_menu{
  color: #000;
}

.header.active .nav_link:hover .items_link{
  color: #000;

}

.icon_arrow_dawn{
  transition: .3s ease all;
}

.nav_link:hover .icon_arrow_dawn{
  transform: translateY(3px);
}


.items_link{
  cursor: pointer;
}


.items_link.active{
  padding: 0 0 3px 0;
  font-weight: 900;
  color: #fff;
}
.items_link.active{
  color: #fff;
}

.header.active .items_link.active{
  color: #000;

}




/* ........................... Home ----------------------- */


.content_slide_home{
  position: relative;
  height: 100%;
  width: 100%;

}

.item_slide{
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.img_slide_home{
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: right;
}

.item_slide.one{
  height: 100%;
  width: 50%;
  background-image:linear-gradient(180deg,rgba(88, 86, 90, 0.1) 50%,rgba(0, 0, 0, 0.84) 100%), url(./img/photoSlideOne.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: right;
  background-origin: padding-box;
  background-color: rgb(65, 64, 64);
  background-blend-mode: hard-light;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.item_slide.two{
  height: 100%;
  width: 50%;
  background-image:linear-gradient(180deg,rgba(88, 86, 90, 0.1) 50%,rgba(0, 0, 0, 0.84) 100%), url(./img/photoSlideTwo.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: right;
  background-origin: padding-box;
  background-color: rgb(65, 64, 64);
  background-blend-mode: hard-light;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.item_slide.tree{
  height: 100%;
  width: 50%;
  background-image:linear-gradient(180deg,rgba(88, 86, 90, 0.1) 50%,rgba(0, 0, 0, 0.84) 100%), url(./img/photoSlideTree.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: right;
  background-origin: padding-box;
  background-color: rgb(65, 64, 64);
  background-blend-mode: hard-light;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.item_slide::before{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #2228;
}
.content_slide_description{
  position: relative;
  margin: 50px 20px;
  display: flex;
  width: 530px;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    transition: .5s ease;
  
}



.titulo{
  font-size: 1.7rem;
  color: rgb(238, 238, 238);
  transition: .5s ease;
}

.description{
  font-size: 4.3rem;
  color: rgb(255, 255, 255);
  font-weight: 100;
  margin: 20px 0 0 0;
  font-family: 'Questrial', sans-serif;


}

.btn_slide_home{
  display: flex;
  margin: 30px 0;
}
.number_slide{
  font-size: 2.5rem;
  font-weight: 100;
  letter-spacing: 1px;
  color: #fff;
  transform: translateY(100px);
  position: relative;
}

.number_slide::before{
  content: "";
  position: absolute;
  top: -112px;
  left:15px;
  height: 2px;
  width: 310%;
  background-color: rgb(204, 204, 204);
}

.content_socials{
  list-style: none;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}


.social_item{
  font-size: 18px;
  color: #fff;
  padding: 10px 0;
  

}

.content_socials img{
  width: 30px;
  height: 30px;
  filter: invert(1);
  transition: 0.7s;
  cursor: pointer;
}

.content_socials img:hover{
     transform: scale(.90);
}

.showBags{
  position: absolute;
  bottom: 35px;
  right: 25px;
  background-color: #FFF2;
  backdrop-filter: blur(15px);
  display:flex;
  justify-content: center;
  align-items: center;
  padding: 15px;
  border-radius: 8px;
}

.icon_modal_show{
  font-size: 15px;
  margin: 0 15px 0 10px;
  color: #fff;
  transition: .5s ease all;
}

.description.modal{
  font-size: 1.2rem;
  padding-right: 5px;
}

.content_description_modal{
  margin: 0 10px 0 0;
    display: flex;
    flex-direction: column;
}

.more_bags{
  color: #fff;
  margin: 10px 0;
  font-family: 'Rajdhani', sans-serif;
  font-size: 18px;
  font-weight: 900;
  display: flex;
  align-items: center;
  cursor: pointer;
}


.more_bags:hover  .icon_modal_show{
  transform: translateX(5px);
}

.modal_show_bag{
    width: 170px;
    height: 170px;
    border-radius: 8px;
    overflow: hidden;
}

.modal_show_bag img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: bottom;
  transition: 0.5s ease;
}

.modal_show_bag:hover  img{
  transform: scale(1.1);
}

.swiper-button-prev , .swiper-button-next{
  opacity: 0;
  visibility: hidden;
  transition: .3s ease all;
}

.swiper-button-next:after, .swiper-rtl .swiper-button-prev:after , .swiper-button-next:after, .swiper-button-prev:after{
  content: 'next';
  font-weight: bolder !important;
  font-size: 20px !important;
  color: #111 !important;
}

.swiper-button-next, .swiper-rtl .swiper-button-prev,.swiper-button-prev, .swiper-rtl .swiper-button-next {
  right: 10px;
  left: auto;
  border: 10px;
  width: 35px !important;
  height: 35px !important;
  background: #fff !important;
  border-radius: 50% !important;
}


.content_slide_home:hover .swiper-button-prev , .content_slide_home:hover .swiper-button-next{
   
   opacity: 1;
   visibility: visible;
  
}

.btn_nosotros.home{
  background-color: var(--color-text-description);
  color: var(--color-text);
}
/* .............................. About ............................ */




.content_about {
