/* -----------------------------  Weekend --------------------- */



.content_titulo_subtitulo{
  text-align: center;
}

.mandarATiendaDeWeekEnd{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.titulo_weekend{
  font-size: 3rem;
  padding: 10px 0;
}

.subtitulo_weekend{
  font-size: 1.7rem;
  font-weight: lighter;
  color: #707070;
}


.content_slider{
 width: 90%;
}

.content_slides{
  margin: 50px 10px;
  display: grid;
