  object-fit: cover;
  object-position: center;
}

.name_bag{
  font-size: 1.5rem;
  font-weight: lighter;
  padding: 10px 0 ;
}

.precio_bag{
  font-size: 1.5rem;
  font-weight: lighter;
  padding: 10px  0;
}



.swiper-pagination-bullet-active{
  background-color: #000000 !important;
  height: 12px !important;
  width: 12px !important;

}

.swiper-pagination-bullet {
  height: 12px !important;
  width: 12px !important;
}


/* -------------------------- Coleccion ---------------------------- */



.content_coleccion_galeria{
  width: 90%;
  margin: 50px 20px;
  display: grid;
  grid-template-columns: 50% 1fr;
  grid-template-rows: 1fr 1fr;
  min-height: 550px;
  gap: 20px;
}

.bag_product.tree{
  position: relative;
  width: 100%;
  display: flex;
  background-repeat: no-repeat;
  background-image:linear-gradient(180deg,rgba(88, 86, 90, 0.1) 50%,rgba(0, 0, 0, 0.84) 100%), url(./img/photoColeccion.jpeg);
  background-size: cover;
  background-position: center;
  background-color: var(--color-text);
  background-blend-mode: hard-light;
}

.bag_product.two{
  position: relative;
  width: 100%;
  display: flex;
  background-repeat: no-repeat;
  background-image:linear-gradient(180deg,rgba(88, 86, 90, 0.1) 50%,rgba(58, 56, 56, 0.84) 100%), url(./img/photoColeccionTwo.jpeg);
  background-size: cover;
  background-position: center;
  background-color: var(--color-text);
  background-blend-mode: hard-light;
}
.bag_product{
  position: relative;
  width: 100%;
  display: flex;
  background-repeat: no-repeat;
  background-image:linear-gradient(180deg,rgba(88, 86, 90, 0.1) 50%,rgba(58, 56, 56, 0.84) 100%), url(./img/photoColeccionTree.jpeg);
  background-size: cover;
  background-position: center;
  background-color: var(--color-text);
  background-blend-mode: hard-light;
  transition: .5s ease all;
}

.bag_product:nth-child(1){
  grid-column: 1 / 1;
  grid-row: 1 / 3;  
}


.bag_product:hover{
  transform: scale(.95);
  cursor: pointer;
  background-image:linear-gradient(180deg,rgba(88, 86, 90, 0.1) 50%,rgba(253, 253, 253, 0.84) 100%), url(./img/photoColeccionTree.jpeg);

}

.bag_product.two:hover{
  background-image:linear-gradient(180deg,rgba(88, 86, 90, 0.1) 50%,rgba(243, 242, 242, 0.84) 100%), url(./img/photoColeccionTwo.jpeg);
}

.bag_product.tree:hover{
  background-image:linear-gradient(180deg,rgba(88, 86, 90, 0.1) 50%,rgba(243, 242, 242, 0.84) 100%), url(./img/photoColeccion.jpeg);

}

.subtitle_coleccion{
  margin: 30px 0 0 30px;
  font-size: 2rem;
  color: #fff;
  font-weight: lighter;
  cursor: pointer;
  width: fit-content;
  height: fit-content;
}
.bag_product:hover .subtitle_coleccion{
  border-bottom: 1px solid #fff;
}

.btn_link_galery{
  position: absolute;
  top:0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
}

