/* -------------------- Load -------------- */ 

.content_circle{
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}


.circle{
  position: relative;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border:6px solid #383836;
  animation: load 1s infinite linear;
}
.circle::after{
  content: "";
  position: absolute;
  top:-10px;
  left: 20px;
  width: 50px;
  height: 45px;
  border-radius: 50%;
  background-color: rgb(255, 255, 255);
}

@keyframes load {

   100%{
     transform: rotate(1turn);
   }

}



